import {h, reactive, ref, Suspense} from 'vue'
import LoadingCard from 'components/LoadingCard.vue'

const useSuspense = () => {
  // const genSuspense = (component, props = {}, fallback = 'loading') => {
  const genSuspense = (slot, keyName=null) => {
    if(!slot.component) slot = {
      component: slot
    }
    const vm = slot.fallback || h(LoadingCard) // TODO: If card loading like card etc
    const props = slot.props || {}
    if(!!keyName) props.key = keyName
    return h(Suspense, null, {
      default: () => h(slot.component, props),
      fallback: () => vm
    })
  }

  return {
    genSuspense
  }
}

const useNoData = () => {
  const genNoData = () => {
    h('span', 'There\'s no data.')
  }

  return {
    genNoData
  }
}

const useTab = component => {
  const data = ref(component)

  return {
    data
  }
}

export {
  useSuspense,
  useTab
}